import React, { Component } from 'react'
import Navbar from './Navbar'
import database from '../data'
import loading from '../images/loading.gif'
import Question from './Question';

export default class Profile extends Component {
    constructor(){
        super();
        this.state = {
            user:'',
            isLoading:true,
            answers:[]
        }
    }
    componentDidMount(){
        const { username } = this.props.match.params;
        let user = null,questions = null,answers;
        if(username){
            user = database.users.filter((user)=> {
                return user.username===username;
            })
           questions = database.questions.filter((question)=>{
                      if(question.asked_by===username){
                          return true;
                      }
                      for(let i=0;i<question.answer_ids.length;i++){
                          if(database.answers[question.answer_ids[i]-1].answered_by===username){
                              return true;
                          }
                      }
                
           })
           console.log(questions); 
        }
        if(user && questions){
            setTimeout(()=>{
                this.setState({user:user[0],isLoading:false,answers:questions})
            },1500);
        }

    }
  render() {
      let content = (
          <div>
              <Navbar />
              <div className="container m-3">
                <div className="row">
                    <div className="col-7">
                       <img src={loading} alt="loading" className="img-responsive" />
                    </div>
                </div>
              </div>
          </div>
      )
    if(!this.state.isLoading && this.state.user===undefined){
        this.props.history.push("/404");

    }
    else if(!this.state.isLoading){
        let answers = Object.values(this.state.answers).map((answer,index)=>{
            // console.table({index,answer})
            return <Question data={answer} key={index}/>;
        })
        content = (
            <div>
                <Navbar />
                <header>
                    <div className="container container-m-5">
                        <div className="row">
                            <div className="col-3">
                            <img src={this.state.user.profile_img} alt="img" className="profile_img"/>
                            </div>
                            <div className="col-7">
                            <p className="title source_code text-center">{this.state.user.name}</p>
                            </div>
                        </div>
                    </div>
                </header>
                <div className="row container-m-5 m-5">
                   <div className="col-md-7 col-9 mb-0">
                        <hr/>
                       <b>Your questions and answers</b>
                       <hr/>
                       {answers}
                   </div>
                </div>
           </div>
        )
    }
    return (
      <div>
          {content}
      </div>
    )
  }
}
