import React, { PureComponent } from 'react'
import {Link } from 'react-router-dom'
import Comment from './Comment'

export default class Answer extends PureComponent {
  state = {
    upvotes:this.props.data.upvotes,
    downvotes:this.props.data.downvotes,
    upvoted:0,
    addComment:false,
    answered_by:this.props.data.answered_by,
    answer:this.props.data.answer,
    accepted:this.props.data.accepted,
    comments:this.props.data.comments,
    comment:{user:'user1',comment:''}
  }
  addComment = ()=>{
    this.setState({addComment:true})
  }
  postComment = (event)=>{
       event.preventDefault();
       console.log(this.state.comment);
       this.setState((prev)=>{
         return {
           comments:[...prev.comments,prev.comment],
           comment:{user:'user1',comment:''},
           addComment:false
          }
       })
  }
  handleInput = (event)=>{
    event.preventDefault();
    let value = event.target.value;
    this.setState({comment:{user:'user1',comment:value}})
  }
  upvote = ()=>{
      if(this.state.upvoted==0 || this.state.upvoted==-1 ){
        if(this.state.upvotes>=0 && this.state.downvotes==0){
          this.setState((prev)=>{
            return {
              upvotes:prev.upvotes+1,
              downvotes:prev.downvotes,
              upvoted:prev.upvoted+1
            }
          })
        }
        else{
          this.setState((prev)=>{
            return {
              upvotes:prev.upvotes,
              downvotes:prev.downvotes+1,
              upvoted:prev.upvoted+1
            }
          })
        }

      }
  }
  downvote = ()=>{
        
            if(this.state.upvoted==0 || this.state.upvoted==1 ){
                if(this.state.upvotes>0){
                  this.setState((prev)=>{
                    return {
                    upvotes:prev.upvotes-1,
                    downvotes:prev.downvotes,
                    upvoted:prev.upvoted-1
                    }
                  })
                }
                else{
                  this.setState((prev)=>{
                    return {
                    upvotes:prev.upvotes,
                    downvotes:prev.downvotes-1,
                    upvoted:prev.upvoted-1
                    }
                  })
                }
            }
  }

  render() {
      
         let total_comments ='No comments yet !';
         if(this.state.comments.length>0){
               total_comments = this.state.comments.map((comment)=>{
                   return <Comment data={comment} key={comment.comment}/>
               })
         }

         let content = (<div className="font-post source_code card m-3">
             <div className="row post_img">
             <div className="col-2 mx-2">
                  <div className="row p-2">
                        <button className="btn btn-sm emoji" name="upvotes" onClick={this.upvote}>{this.state.upvotes}<span className="emoji">üëç</span> </button>
                  </div>
                  <div className="row p-2">
                        {this.state.accepted===true?<span className="accepted emoji" title="accepted by user">‚úÖ</span>:null}
                  </div>
                  <div className="row p-2">
                        <button className="btn btn-sm emoji" name="downvotes" onClick={this.downvote}>{this.state.downvotes}<span className="emoji">üëé</span>  </button>
                  </div>
               </div>
               <div className="col-9 p-2">
                  <div dangerouslySetInnerHTML={{__html:this.state.answer}}  className="paragraph"/>
                  <span className="text-right paragraph user"><Link to={"/profile/"+this.state.answered_by} className="nav-link" >-: {this.state.answered_by}</Link></span>
                  {/* <p className="time">{this.props.data.time}</p> */}
               </div>
             </div>
             <div className="bg_comment p-0">
                <div className="p-2">
                    {total_comments}
                </div>
                <div className="row mx-2" style={{visibility:this.state.addComment?'hidden':'visible'}} >
                      <button className="btn btn-success btn-sm" onClick={this.addComment}>comment</button>
                </div>
                <div className="row" style={{display:this.state.addComment?'flex':'none'}}>
                  <div className="col-7">
                      <input type="text" className="form-control paragraph" name="comment" placeholder="Comment." value={this.state.comment.comment} onChange={this.handleInput}/>
                  </div>
                  <div className="col-2">
                    <button className="btn btn-success btn-sm" onClick={this.postComment}>post</button>
                  </div>
                </div>
             </div>
         </div>
         )
    return (
      <div>
        {content}
      </div>
    )
  }
}
