import React, { Component } from 'react'
import {Link} from 'react-router-dom'
export default class Comment extends Component {
  render() {
    return (
      <div className="block">
        <div className="row m-auto">
           <span className="paragraph float_right p-1">{this.props.data.comment}</span>
           <span className="paragraph user float_right p-1"><Link to={"/profile/"+this.props.data.user}> -: {this.props.data.user}</Link></span>
        </div>
      </div>
    )
  }
}
